.mainImg { 
    position: absolute;
    background: linear-gradient(to right,#fdfdfd67,#fdfdfd67, #fdfdfd67), url(../../images/clouds-731260.jpg) no-repeat top center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
}

.favoritesItemsLayout{
    display: flex;
    justify-content: space-evenly;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 20px;
    width: 100%;
    text-align: center;
}

.starImage{
    width: 200px;
    height: 200px;
    margin-left: 50%;
    transform: translateX(-50%);
    opacity: 0.5;
    margin-top: 100px;
}

@media only screen and (max-width: 750px){
    .favoritesItemsLayout{
        flex-direction: column;
    }
    .starImage{
        width: 50px;
        height: 50px;
    }
}